@import "../../Styles/Variables/Variables.scss";

.profileChoose {
  max-width: 100vw;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow-y: hidden;
  overflow-x: hidden;

  .flexContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;

    .profileCard {
      cursor: pointer;
      max-width: 190px;
      margin: 1rem;
      @media (max-width: 500px) {
        max-width: 100px;
      }
      .avatar {
        width: 100%;
      }

      .profileName {
        text-align: center;
      }
    }
  }
}
