@import "../../Styles/Variables/Variables.scss";

.modal,
.overlay {
  width: 100vw;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  position: fixed;
  z-index: 1000;
  overflow-y: auto;
}

.overlay {
  background-color: rgba(19, 19, 19, 0.4);
  display: flex;
  justify-content: center;
}

.modalContent {
  width: 55%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  background-color: $primary;
  border-radius: 5px;
  margin: 1rem;

  @media (max-width: $XL-width) {
    width: 60%;
  }

  @media (max-width: 1000px) {
    width: 75%;
  }

  .imageContainer {
    position: relative;
    width: 100%;
    height: 45vh;

    img {
      width: 100%;
      height: 45vh;
      object-fit: cover;
      border-top-right-radius: 5px;
      border-top-left-radius: 5px;
    }

    .imageDetailsContainer {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(
        0deg,
        rgb(19, 19, 19, 1) 1%,
        rgba(19, 19, 19, 0.1) 65%
      );

      .imageDetails {
        position: absolute;
        bottom: 0;
        margin: 0 0 2rem 2rem;
        max-width: 45%;

        .imageDetailsTitle {
          font-size: 1.5rem;
          margin-bottom: 1rem;
        }

        .imageDetailsPlayButton {
          @include button;
        }
      }
    }
  }
  .details {
    display: flex;
    margin: 2rem;
    column-gap: 2rem;

    @media (max-width: $L-width) {
      flex-direction: column;
    }

    .info {
      display: flex;
      flex-direction: column;
      gap: 1rem;

      .runtime {
        display: flex;
        flex-direction: row;
        gap: 1rem;

        @media (max-width: $L-width) {
          flex-direction: column;
        }
      }
    }
  }

  .credits {
    display: flex;
    flex-direction: column;
    row-gap: 0.625rem;
    margin-top: 1rem;

    .creditsElements {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      column-gap: 0.5rem;
      min-width: 130px;
      color: $navHover;

      .creditsParagraph {
        font-size: 0.875rem;
        color: $fontPrimary;
      }
    }
  }

  .similar {
    margin: 2rem;

    .similarContainer {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-evenly;
      row-gap: 1rem;

      .similarCard {
        max-width: 250px;
        background-color: $modalCardColor;
        border-radius: 5px;

        // @media (max-width: 1270px) {
        //   max-width: 300px;
        //   justify-content: center;
        // }

        .cardImage {
          width: 100%;
          object-fit: cover;
          border-top-left-radius: 5px;
          border-top-right-radius: 5px;
        }

        .cardDetails {
          margin: 1rem;
          display: flex;
          flex-direction: column;
          gap: 1rem;
        }
      }
    }
  }

  .moreDetails {
    margin: 2rem;
  }
}
