@import "../../Styles/Variables/Variables.scss";

nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  list-style-type: none;
  z-index: 999;

  .navContainer {
    margin: $marginLR;
    display: flex;
    align-items: center;
    font-size: $primaryFs;
    font-weight: 100;

    @media (max-width: $Mobile-width) {
      margin: $mobileMarginLR;
    }
  }

  .navLogo {
    width: 100px;
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  ul {
    display: flex;
    list-style-type: none;

    li {
      cursor: pointer;
      &:hover {
        color: $navHover;
        transition: color 0.4s;
      }
    }
    .avatar {
      width: 55px;
      display: flex;
      align-items: center;
      justify-content: flex-end;

      img {
        width: 32px;
      }
    }
  }

  .dropdown {
    margin: 0 2rem;
    position: relative;
    padding: 1rem 0 1.25rem 0;

    .dropdownHeading {
      display: none;
    }

    @media (max-width: $L-width) {
      margin: 0;
      width: 150px;
      &:hover {
        background: $primary;
        border-top-left-radius: 15px;
        border-top-right-radius: 15px;
      }

      &:hover .dropdownContent {
        display: flex;
      }

      .dropdownHeading {
        display: flex;
        width: 150px;
        padding: 0 1rem;
      }
    }

    .dropdownContent {
      gap: 1.25rem;

      @media (max-width: $L-width) {
        position: absolute;
        display: none;
        flex-direction: column;
        top: 100%;
        min-width: 150px;
        background-color: $primary;
        border-bottom-right-radius: 15px;
        border-bottom-left-radius: 15px;

        li {
          padding: 0 1rem;
        }

        :last-child {
          padding: 0 1rem 1.25rem 1rem;
        }
      }
    }
  }

  .secondNav {
    margin-left: auto;

    @media (max-width: $L-width) {
      width: 70px;
    }
  }
}
