@import "../../Styles/Variables/Variables.scss";

.slider {
  padding: 0 1rem;

  .sliderHeading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: $marginLR;

    @media (max-width: $Mobile-width) {
      margin: $mobileMarginLR;
    }
    .sliderTitle {
      z-index: 1;
    }

    .pagination {
      width: auto;

      @media (max-width: $L-width) {
        display: none;
      }
      .swiper-pagination-bullet {
        width: 15px;
        height: 3px;
        border-radius: 1px;
        background: rgba(255, 255, 255, 0.2);
        opacity: 1;
        margin: 0 1px;
      }

      .swiper-pagination-bullet-active {
        background: rgba(255, 255, 255, 0.5);
      }
    }
  }

  .sliderWrapper {
    display: flex;
    margin-top: 1rem;
    padding: 0 3.75rem;
    overflow: visible;
    z-index: 1;
    @media (max-width: $Mobile-width) {
      padding: 0 1rem;
    }

    .indicator {
      position: absolute;
      z-index: 2;
      height: 100%;
      width: 3.7rem;
      @media (max-width: $L-width) {
        display: none;
      }

      & > svg {
        width: 100%;
        height: 100%;
      }

      &.left {
        left: 0;
      }

      &.right {
        right: 0;
      }
    }

    .cardDetails {
      width: 100%;
      position: absolute;
      background-color: #1a1a1a;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
      bottom: 0;
      transform: translateY(95%);
      padding: 0.5rem;

      .btnContainer {
        display: flex;
        column-gap: 0.5rem;
        padding: 0.5rem 0;
        .cardBtn {
          @include cardButton;
          background-color: $playButtonColor;
        }
        .infoBtn {
          background-color: $infoButtonColor;
          color: $fontPrimary;
        }
      }
    }

    .sliderCard {
      min-width: 20%;
      position: unset;
      cursor: pointer;
      .card {
        .sliderCardImg {
          width: 100%;
          display: block;
          aspect-ratio: 16/9;
          border-radius: 5px;
        }
      }
    }

    .sliderCardPoster {
      min-width: 10%;
      position: unset;
      cursor: pointer;

      .posterCard {
        display: flex;
        align-items: center;

        .cardIndex {
          font-size: 12rem;
          color: $modalCardColor;

          @media (max-width: $L-width) {
            font-size: 8rem;
          }

          @media (max-width: $Mobile-width) {
            font-size: 6rem;
          }
        }

        .sliderCardImgPoster {
          width: 45%;
          border-radius: 5px;
        }
        .cardPosterDetails {
          display: flex;
          flex-direction: column;
          .sliderCardImg {
            width: 100%;
            aspect-ratio: 16/9;
            border-radius: 5px;
          }
        }
      }
    }
  }
  .sliderWrapper:hover {
    z-index: 2;

    .cardDetails {
      z-index: 2;
    }
  }
}

.rowsSection {
  display: flex;
  flex-direction: column;
  row-gap: 1rem;
  margin-top: -100px;
  overflow: hidden;

  @media (max-width: $Mobile-width) {
    margin-top: -50px;
  }
}
