@import "../../Styles/Variables/Variables.scss";

.testSection {
  .heroContainer {
    position: relative;
    width: 100%;
    img {
      width: 100%;
      height: 90vh;
      object-fit: cover;

      @media (max-width: $L-width) {
        height: 50vh;
      }
    }

    .movieDetailsContainer {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      right: 0;
      background: linear-gradient(
        0deg,
        rgb(19, 19, 19, 1) 1%,
        rgba(19, 19, 19, 0.1) 65%
      );

      .movieDetails {
        margin: $marginLR;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: center;
        height: 100%;
        width: 40%;

        @media (max-width: $L-width) {
          width: 60%;
        }

        @media (max-width: $Mobile-width) {
          margin: $mobileMarginLR;
        }

        .movieDetailsTitle {
          font-size: $titleFS;
          font-weight: 200;

          @media (max-width: $L-width) {
            font-size: 2rem;
          }
        }

        .movieDetailsOverview {
          font-size: $primaryFs;
        }
      }

      .buttonContainer {
        display: flex;
        margin-top: 1rem;
        .playButton {
          @include button;
          background-color: $playButtonColor;
        }
        .infoButton {
          @include button;
          background-color: $infoButtonColor;
          color: $fontPrimary;
          min-width: 160px;
          margin-left: 1rem;
        }
      }
    }
  }
}
