@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Regular.ttf) format("truetype");
  font-weight: 100;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Bold.ttf) format("truetype");
  font-weight: 200;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url(../fonts/Roboto-Black.ttf) format("truetype");
  font-weight: 300;
}

@mixin button {
  outline: none;
  border: none;
  font-size: 1.25rem;
  display: flex;
  align-items: center;
  column-gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: 5px;
  cursor: pointer;
  font-family: "Roboto";
  font-weight: 200;
}

@mixin cardButton {
  outline: none;
  border: none;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

$marginLR: 0 3.75rem;
$mobileMarginLR: 0 1rem;

$titleFS: 3rem;
$primaryFs: 0.875rem;

$primary: #131313;
$secondary: #db202c;
$fontPrimary: #fff;
$fontSecondary: rgba(255, 255, 255, 60);
$navHover: #b3b3b3;
$playButtonColor: #fff;
$infoButtonColor: rgba(109, 109, 110, 0.7);
$modalCardColor: #2f2f2f;
$indicatorHover: rgba(19, 19, 19, 0.2);

//media querries
$XL-width: 1024px;
$L-width: 850px;
$Mobile-width: 425px;
